import { Component, OnInit } from '@angular/core';
import {CovidDataService} from '../covid-data.service';
import {HostListener} from '@angular/core';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  public covidData = {};
  public stats = [];
  public innerWidth:any;
  public statesChart = {
    'states':[
      ['Maharashtra',72,'red',72],
      ['Kerala',52,'red',52],
      ['Delhi',26,'red',26],
      ['Uttar Pradesh',26,'red',26],
      ['Rajasthan',23,'red',23],
      ['Telangana',21,'red',21],
      ['Karnataka',19,'red',19],
      ['Haryana',18,'red',18],
      ['Gujarat',14,'red',14],
      ['Ladakh',13,'orange',13],
      ['Punjab',13,'Orange',13],
      ['Chandigarh',5,'orange',5],
      ['Tamil Nadu',6,'',6],
      ['Andhra Pradesh',5,'darkyellow',5],
      ['J&K',4,'darkyellow',4],
      ['[West Bengal',4,'yellow',4],
      ['Madhya Pradesh',4,'yellow',4],
      ['Uttarakhand',3,'green',3],
      ['Odisha',2,'darkgreen',2],
      ['Himachal Pradesh',2,'darkgreen',2],
      ['Puducherry',1,'darkgreen',1],
      ['Chhattisgarh',1,'darkgreen',1],
      ['Assam',1,'darkgreen',1],
      ['BIHAR',1,'darkgreen',1]],
    'type' :'BarChart',
    'columnNames' : ['State', 'Positive Cases',{ role: 'style' }, { role: 'annotation' }],
    'chartTitle' : 'States and UTs'
  }
  public countries = [];
  public country = 'India'
  public history = {};
  chartTitle = '';
  type = 'PieChart';
  data = [
    ['Above 60', 100],
    ['Below 60', 232],
  ];
  columnNames = ['Browser', 'Percentage'];
  options = {
    is3D: true,
    height : 900
  };
  width = 350;

  constructor(private covidDataService: CovidDataService){
    this.countries = ['India','Usa', 'Italy', 'China', 'Pakistan', 'Canada', 'Germany', 'France', 'Spain', 'Australia'];


  }
  ngOnInit(){
    this.getCovidUpdate(this.country)
    this.getHistory(this.country)
    this.innerWidth = window.innerWidth-50;
  }
  @HostListener('window:resize', ['$event'])
  onResize(event) {
    this.innerWidth = window.innerWidth-50;
    console.log(this.innerWidth)
  }
  public getCovidUpdate(country){
    // console.log(country)
    this.covidDataService.getData(country).subscribe(
      (data)=>{this.covidData = data, this.stats = data['latest_stat_by_country'][0]},
      (er)=>console.log(er)
    )
  }
  public getHistory(country){
    this.covidDataService.getHistory(country).subscribe(
      (data)=>{this.history = data,console.log(this.history)},
      (er)=> console.log(er)
    )
  }


  title = 'covid19 Status ';
}
